html {
  box-sizing: border-box;
  color: white;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.app {
  width: 100%;
  margin-top: 64px;
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-wrapper {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  max-width: 640px;
}

.input-wrapper > input {
  padding: 8px;
}

.input-wrapper > button {
  margin-top: 8px;
  padding: 8px;
  cursor: pointer;
  background-color: #EDD75A;
  color: black;
  border-radius: 4px;
}

.message {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 8px 16px;
  margin: 8px;
  border-radius: 4px;
  border: 0.5px solid black;
  max-width: 640px;
}

.empty-messages-box {
  padding: 16px;
  font-size: 20px;
}

.message-header {
  display: flex;
  justify-content: space-between;
}

.name {
  font-weight: 600;
}

.chat {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 720px;
  background-color: #372DA1;
  padding: 8px;
  height: calc(100vh - 128px);
}
.message-box {
  width: 100%;
  overflow-y: scroll;
}

.new-message-form-wrapper {
  width: 100%;
  padding: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}